import React from "react";
import { Link } from "react-router-dom";
import FadeIn from "background-image-fade-in";

class Item extends React.Component {
  componentDidMount() {
    FadeIn(".background-image", 250);
  }

  render() {
    const { image, title, link, index } = this.props;
    const attr = {};
    attr["data-src"] = image;

    const animationDuration = `${500 + index * 250}ms`;

    return (
      <Link
        style={{ animationDuration }}
        to={`/${link}/${index + 1}`}
        className="list-item"
      >
        <div className="background-image" {...attr} />
        <h4>
          {title}
        </h4>
      </Link>
    );
  }
}

export default ({ items, link }) =>
  <div className="list-item-container">
    {items.map((item, index) =>
      <Item {...item} key={index} index={index} link={link} />
    )}
  </div>;



