import React from "react";
import { connect } from "react-redux";
import List from "../components/list";

const component = ({ items }) =>
  <div>
    <h1>Projects</h1>
    <List items={items} link="projects" />
  </div>;

export default connect(({ Projects }) => ({ items: Projects.items }))(
  component
);



